@import url("https://fonts.googleapis.com/css?family=Noto+Sans|Roboto:500");
html, body {
  font-size: 16px;
  background: #ff9966;
  background: -webkit-gradient(linear, left top, right top, from(#ff5e62), to(#ff9966));
  background: -webkit-linear-gradient(left, #ff5e62, #ff9966);
  background: -o-linear-gradient(left, #ff5e62, #ff9966);
  background: linear-gradient(to right, #ff5e62, #ff9966);
  text-align: center;
  font-family: 'Noto Sans', sans-serif; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



